"use client";

import React from 'react';
import { useSelector } from 'react-redux';
import BottomBarWrapper from '@/components/bottomBar/BottomBarWrapper';
import { BottomSection, BottomStatus } from '@/components/bottomBar/BottomBarWrapper';
import { BottomIconCard } from '@/components/bottomBar/BottomIconCard';
import SdCardOutlinedIcon from '@mui/icons-material/SdCardOutlined';
import InspectState from '@/components/debugging/InspectState';
import { FolderBrowserSettings } from '@/components/bottomBar/folder/Settings';

const SectionDebug = () => {
    const enableDebugging = useSelector(state => state.settings.enableDebugging);
    if (!enableDebugging) return null;
      return (
        <BottomSection title="Debug">
            <BottomIconCard title="Inspect State" icon={<SdCardOutlinedIcon/>}>
                <InspectState />
            </BottomIconCard>

        </BottomSection>
      )
  }


const FolderBottomBar = React.memo(function FolderBottomBar() {
    const rootFolder = useSelector(state => state.settings.folderBrowserRootFolder);
    const viewingItemId = useSelector(state => state.folderBrowser.viewingItemId);

    return (
        <BottomBarWrapper settings={<FolderBrowserSettings />}>

            <BottomStatus k="Root" v={rootFolder} />
            <BottomStatus k="Viewing" v={viewingItemId} />

            <SectionDebug />
        </BottomBarWrapper>
    );
});

export default FolderBottomBar;